section.clients {
    // background-color: #999;

    .text_block {
        margin-top: calc(50px + 150/1920*100vw);
        width: 100%;
        max-width: 630px;
        color: white;

        .title {
            font-size: 30px;
            font-weight: 700;
        }

        p {
            margin-top: 25px;
            font-weight: 300;
            line-height: 1.7;
        }

        @media(max-width:1000px) {
            .title {
                font-size: 26px;
            }

            p {
                margin-top: 16px;
            }
        }
        @media(max-width: 560px) {
            .title {
                font-size: 22px;
            }

            p {
                margin-top: 12px;
            }
        }
    }

    ul.logos {
        margin-top: calc(30px + 60/1920*100vw);
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        align-items: center;
        justify-items: center;

        li {
            display: flex;
            width: max-content;
            padding: 50px 15px;

            a {
                width: max-content;
            }
        }

        @media(max-width:1000px) {
            grid-template-columns: 1fr 1fr 1fr;

            li {
                padding: 40px 10px;

                img {
                    transform: scale(0.8);
                }
            }

        }

        @media(max-width:770px) {
            grid-template-columns: 1fr 1fr;

            li {
                padding: 30px 10px;

                img {
                    transform: scale(0.8);
                }
            }
        }

        @media(max-width:400px) {
            grid-template-columns: 50% 50%;

            li {
                padding: 30px 0;

                img {
                    transform: scale(0.7);
                }
            }
        }


    }
}