section.blabla {
    margin-top: 88px;
    color: white;

    h3 {
        width: calc(150px + 550/1920*100vw);
        max-width: 700px;
        line-height: 1.7;

        @media (max-width:600px) {
            width: auto;
            font-size: 16px;

        }

        @media (max-width:400px) {
            font-size: 14px;
        }
    }
}