@font-face {
    font-family: Montserrat;
    src: url(../fonts/Montserrat/Montserrat-Light.ttf);
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: Montserrat;
    src: url(../fonts/Montserrat/Montserrat-Regular.ttf);
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: Montserrat;
    src: url(../fonts/Montserrat/Montserrat-SemiBold.ttf);
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: Montserrat;
    src: url(../fonts/Montserrat/Montserrat-Bold.ttf);
    font-weight: 900;
    font-style: normal;
}

@font-face {
    font-family: ProximaNova;
    src: url(../fonts/Roboto/Roboto-Regular.ttf);
    font-weight: normal;
    font-style: normal;
}


._text_size_l {
    font-size: calc(8px + 16/1920*100vw);

    @media (min-width:1920px) {
        font-size: 22px;
    }
}

._text_size_lx {
    font-size: calc(8px + 32/1920*100vw);

    @media (min-width:1920px) {
        font-size: 40px;
    }
}
._text_size_xl {
    font-size: calc(8px + 54/1920*100vw);

    @media (min-width:1920px) {
        font-size: 62px;
    }
}