._template_button_style_0 {
    // стандартная кнопка
    display: inline-block;
    position: relative;
    padding: calc(0px + 10/1920*100vw) calc(8px + 20/1920*100vw);
    border: 1px solid white;
    font-size: 16px;
    font-weight: 700;
    // line-height: 250%;
    color: white;
    text-transform: uppercase;
    background-color: transparent;
    overflow: hidden;
    z-index: 0;
    transition: 400ms linear 0ms;
    white-space: nowrap;

    &::before {
        content: "";
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        inset: 0;
        transform: translateX(-100%);
        background-color: rgb(255, 255, 255);
        z-index: -1;
        transition: 200ms linear 100ms;
        opacity: 0.3;
    }

    &:hover,
    &.hover {
        color: black;

        &::before {
            transform: translateX(0%);
            opacity: 1;
        }
    }
}

._template_underline_style_0 {
    //  подчеркивание при наведении с лева направо
    position: relative;
    white-space: nowrap;

    &::after {
        content: "";
        position: absolute;
        bottom: 0px;
        display: block;
        width: 0%;
        height: 1px;
        border-radius: 1px;
        background-color: white;
        transition: 200ms ease-out;
        transition-delay: 100ms;
    }

    &:hover::after,
    &.hover::after {
        width: 100%;
        transition-delay: 0ms;
    }

}

._template_underline_style_1 {
    //  подчеркнуто со старта, но присутствует реакция на наведение
    position: relative;
    white-space: nowrap;

    &::after {
        content: "";
        position: absolute;
        bottom: -1px;
        right: 0;
        display: block;
        width: 100%;
        height: 1px;
        border-radius: 1px;
        // background-color: red;
        background-image: linear-gradient(to left,
                white, white);
        // background-size: 50% 100%;
        background-repeat: no-repeat;


    }

    &:hover::after {
        animation: underlineAnim 500ms ease-out;
    }

    @keyframes underlineAnim {
        from {
            background-size: 100% 100%;
            background-position: right;
        }

        50% {
            background-size: 0% 100%;
            background-position: right;
        }

        51% {
            background-position: left;
        }

        to {
            background-size: 100% 100%;
            background-position: left;

        }
    }
}

._template_hover_scale_anim {
    &:hover {
        transform: scale(1.2);
        transition: 0.1s ease-out 50ms;
    }
}

._template_hover_climbUp_anim {
    transition: 0.5s ease-out 50ms;

    &:hover:not(.climbUpBan),
    &:hover .climbUpTARGET {
        transform: translateY(-3%);

    }

    .climbUpTARGET {
        transition: 0.3s ease-out 50ms;

    }
}