header {
    position:fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: calc(10px + 15/1920*100vw) calc(10px + 20/1920*100vw) calc(8px + 15/1920*100vw);
    // background-color: #999;
    z-index: 10;

    .wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        &.wrapper_left {
            .logo svg {
                width: calc(40px + 45/1920*100vw);

                path {
                    fill: white;
                }
            }

            .button {
                margin-left: calc(20px + 80/1920*100vw);

                .short_title {
                    display: none;
                }

                @media (max-width:1200px) {
                    font-size: 14px;
                }

                @media (max-width:800px) {
                    margin-left: calc(15px + 10/1920*100vw);

                    .full_title {
                        display: none;
                    }

                    .short_title {
                        display: block;
                    }
                }

            }
        }

        &.wrapper_right {
            display: flex;
            justify-content: space-between;
            text-transform: uppercase;


            ul {
                display: flex;
                justify-content: space-between;

                &.short_nuv {
                    display: none;

                    svg {
                        width: 20px;
                        fill: white;
                    }

                    @media (max-width:350px) {
                        svg {
                            width: 15px;
                        }

                        li {
                            margin-right: calc(10px + 10/1920*100vw);
                        }

                        li:last-child {
                            margin-right: calc(15px);
                        }
                    }

                }

                li {
                    margin-right: calc(20px + 10/1920*100vw);
                }

                a {
                    // display: block;
                    color: white;
                    font-weight: 700;
                    font-size: 14px;
                }

                @media (max-width:720px) {
                    &.full_nav {
                        display: none;
                    }

                    &.short_nuv {
                        display: flex;
                    }
                }

            }

            .burger {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                width: calc(27px + 8/1920*100vw);
                height: calc(22px + 5/1920*100vw);
                // background-color: red;
                overflow: hidden;

                .line {
                    position: relative;
                    left: -130%;
                    height: 4px;
                    width: 230%;
                    border-radius: 1px;
                    background-color: transparent;
                    background-image: linear-gradient(90deg, white 48%, transparent 48%, transparent 56%, white 56%);

                }

                &:hover {
                    cursor: pointer;
                    @media (min-width:720px) {
                        .line {
                            animation: animate_burgerLine 0.8s ease-out infinite;
                            animation-delay: calc(var(--i)*110ms);
                        }
                    }
                }
            }
        }
    }

    @media (max-width:720px) {
        background-color: rgba(0, 0, 0, 0.8);
    }
}

@keyframes animate_burgerLine {

    0% {
        left: -130%;
    }

    80%,
    100% {
        left: 0%;
    }
}