section.comments {
    margin-top: calc(40px + 100/1920*100vw);
    // background-color: #999;
    color: white;

    .block_title {
        font-size: 24px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: .07;
    }

    ul.comments {
        margin-top: calc(50px + 100/1920*100vw);
        display: grid;
        align-items: baseline;
        grid-template-columns: 1fr 1fr 1fr;
        grid-column-gap: 10%;

        @media (max-width:1000px){
        grid-template-columns: 1fr;
        }
        li {

            @media (max-width:1000px) {
                    &:not(:last-child){
                        margin-bottom: calc(50px + 50/1000*100vw);
                    }
                }
            a.text {
                font-size: 14px;

                &:hover{
                    cursor: pointer;
                }
                p {
                    margin-bottom: 10px;
                    line-height: 1.7;
                }
            }

            .line {
                margin-top: 10px;
                position: relative;
                height: 1px;
                --line_color: #8E8E8E;
                background-color: var(--line_color);


                .angle {
                    position: relative;
                    left: 80px;
                    height: 20px;
                    width: 0;
                    border-left: 1px solid var(--line_color);

                    &:before {
                        content: "";
                        display: block;
                        position: absolute;
                        background-color: transparent;
                        border-left: 30px solid var(--line_color);
                        border-bottom: 20px solid transparent;
                    }

                    &:after {
                        content: "";
                        display: block;
                        position: absolute;
                        background-color: transparent;
                        border-left: 28px solid var(--bodyColor);
                        border-bottom: 19px solid transparent;

                    }
                }

            }

            .client {
                margin-top: 40px;
                display: flex;
                align-items: center;

                .logo {
                    margin-right: 15px;
                    img{
                        max-height: 80px;
                    }
                }

                .name {
                    font-weight: 700;

                }

                .info_about_client {
                    margin-top: 5px;
                    color: #8E8E8E;
                    line-height: 24px;
                }
            }
        }
    }
}