section.portfolio_work {
    position: relative;
    margin-top: calc(40px + 150/1920*100vw);

    ul {
        li {
            display: flex;
            align-items: center;
            position: relative;
            height: calc((80px + 1180/1920*100vw)/2);
            max-height: 630px;

            &:not(:first-child){
                margin-top: calc(40px + 280/1920*100vw);
            }

            .background {
                position: absolute;
                right: 0;
                z-index: -1;
                height: 100%;
                opacity: 0.3;
                overflow: hidden;

                img {
                    height: 100%;
                    transform: scale(1);
                    transform-origin: center center;
                    transition-duration: 500ms;
                }

                .frame {
                    position: absolute;
                    top:0;
                    left: 0;
                    height: 100%;
                    width: 5%;
                    background-color: var(--bodyColor);
                    z-index: 5;

                    @media (max-width:770px) {
                        display: none;
                    }
                    &.right_frame {
                        left:auto;
                        right: 0;

                    }
                }



            }
            .content {
                // background-color: rgb(171, 159, 249);
                width: 1000px;
                color: white;

                .line_decoration {
                    width: 20px;
                    height: 2px;
                    background-color: red;
                    transition: 0.2s ease-out 100ms;

                    &.hover {
                        width: 90px;
                    }
                }

                h3 {
                    margin-top: 2%;
                    font-weight: 700;
                }

                .button {
                    margin-top: 5%;
                    padding-top: 2%;
                    padding-bottom: 2%;
                    font-size: 14px;
                }
            }

            &:hover {

                img {
                    transform: scale(1.05);
                }
            }
        }
    }

    a.show_works{
        display: block;
        text-align: center;
        margin-top: calc(40px + 120/1920*100vw);
        color:white;
        sup{
            font-size: 70%;
            vertical-align: super;
        }
    }
}