section.main {
    position: relative;
    top: 0;
    width: 100%;
    // max-width: 100vw;
    // height: calc(100vw*0.6);
    // max-height: 100vh;
    // max-height: max-content;
    background-color: #333;
    z-index: 1;

    .live_background {
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: hidden;

        img {
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
            transform-origin: center center;

            &:last-child {
                animation: live_background 6s ease-out;
            }


            &.active {
                animation: live_background_shift 6s ease-out;
            }

            @keyframes live_background {
                from {
                    transform: scale(1.2);
                }

                to {
                    transform: scale(1);
                }
            }

            @keyframes live_background_shift {
                from {
                    opacity: 0;
                    transform: scale(1.2);
                }

                10% {
                    opacity: 1;
                }

                to {
                    transform: scale(1);
                }
            }
        }
    }

    .blackout {
        position: absolute;
        inset: 0;
        background-color: rgba(0, 0, 0, .6);
    }

    .content {
        margin-top: 50px;
        display: flex;
        align-items: center;
        position: relative;
        padding: calc(90px + 250/1920*100vw) 0 calc(60px + 250/1920*100vw);
        margin: 0 auto;
        // width: calc(60px + 1210/1920*100vw);
        // max-width: 1260px;
        // box-sizing: border-box;
        color: white;
        // background-color: rgb(103, 103, 103);


        .title {
            font-weight: 700;

        }

        .selected_information {
            display: flex;
            margin-top: 80px;
            font-weight: 300;

            @media (max-width:400px) {
                margin-top: 40px;
            }

            ul {
                display: flex;

                @media (max-width:400px) {
                    flex-direction: column;
                }

                li {

                    @media (max-width:400px) {
                        margin-top: 12px;
                    }

                    &:not(:last-child) {
                        margin-right: calc(10px + 90/1920*100vw);
                    }

                    a {
                        display: inline-block;
                    }
                }
            }
        }

        .buttons {
            display: flex;
            width: max-content;
            margin-top: 80px;

            @media (max-width:900px) {
                flex-direction: column;
                align-items: start;
                &>*{
                    font-size: 12px;
                    padding: 10px 20px;
                    width: 100%;
                }
            }

            @media (max-width:400px) {
                margin-top: 40px;
            }

            &>*:not(:last-child) {
                margin-right: calc(40/1920*100vw);

                @media (max-width:900px) {
                    margin-bottom: 20px;
                }
            }
        }
    }
}